<template>

  <div class="hello">
    <Loader />
    <h5>{{WareName}}</h5>
    <div class="table-responsive">
         <table class="table table-striped table-sm">
            <colgroup>
             <!--  <col span="1" style="width: 56%;">
               <col span="1" style="width: 22%;">
               <col span="1" style="width: 10%;">
               <col span="1" style="width: 12%;">-->
               <col span="1" style="width: 56%;">
               <col span="1" style="width: 22%;">
               <col span="1" style="width: 22%;">
            </colgroup>
            <tbody id="tbody">
             <!-- <th>
                <td>Наименование</td>
               <td>Магазин</td>
               <td>Остаток</td>
               <td>Цена</td>
              </th>-->
            <tr v-for="(item, index) in rows" v-bind:key="index">
            <!--   <td>{{item.Name}}</td>-->
               <td>{{item.Store}}</td>
               <td>{{item.Remain}} шт.</td>
               <td>{{item.Price}} руб.</td>
            </tr>
            </tbody>
         </table>
      </div>
      <StreamBarcodeReader
    @decode="onDecode"
    @loaded="onLoaded"
      ></StreamBarcodeReader>
  </div>
</template>

<script>
import  {StreamBarcodeReader} from "vue-barcode-reader";
import Loader from './Loader.vue';
   import 'bootstrap/dist/css/bootstrap.css';
   import 'bootstrap-vue/dist/bootstrap-vue.css';
export default {
  name: 'HelloWorld',
  data() {
           return {
               rows: {},
               WareName:""
           }
  },
  mounted: function () {
    this.load(true);
},
  methods: {
    onLoaded(){
      this.load(false);
    },
    onDecode (result) {
      //let vm = this;
      this.load(true);
      this.WareName = ""; 
      fetch("https://utserver.pl:9090/api/Ware/" + result).then(r => {
      //fetch("http://192.168.200.110:888/api/Ware/" + result).then(r => {
                   return r.json();
               }).then(data => { 
                 this.rows = data; 
                 this.WareName = data[0].Name;
               }).finally(() => {
                   this.load(false);
               });
  },
  load(stop) { //in case there'd be more code
               if (stop)
                   document.getElementById('loader').style.display = "";
               else
                   document.getElementById('loader').style.display = "none";
           }
},
components: {
    StreamBarcodeReader,
    Loader
  }
}
</script>

